import React, {Component} from 'react';
import {Link, NavLink} from 'react-router-dom';
import './_appHeader.css';
import '../../Containers/Home/_home.css';

class AppHeader extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currentUser: this.props.currentUser ? this.props.currentUser : null,
      isSearch: false
    }
  }

  handleClickInput = () => {
    this.setState({
      isSearch: !this.state.isSearch
    },()=>this.props.callBackFromApp(this.state.isSearch))
  };

  render() {
    // let searchInHeader;
    // if(this.props.renderInputHeader === true) {
    //   searchInHeader =
    //       <div className="search">
    //         <span className="fa fa-search"></span>
    //         <input placeholder="Tìm kiếm"
    //         />
    //       </div>
    // } else searchInHeader=null;
    const {currentUser, isSearch} =this.state;

    return (
        <header className="app-header">
          <div className="container-fluid" style={{paddingLeft:"40px"}}>
            <div className="row">
              <div className="col-md-5 app-branding">
                <div className="graf-layout">
                  <div className="graf-circle"></div>
                  <div className="graf-circle"></div>
                  <div className="graf-circle"></div>
                  <div className="graf-circle"></div>
                  <div className="graf-circle"></div>
                  <div className="graf-circle"></div>
                  <div className="graf-circle"></div>
                  <div className="graf-circle"></div>
                  <div className="graf-circle"></div>
                  <div className="graf-circle"></div>
                  <div className="graf-circle"></div>
                </div>
                <Link to="/profile" className="app-title">Rebook</Link>
                <div className="search">
                  <span className="fa fa-search"></span>
                  <input placeholder="Tìm kiếm" readOnly
                         style={isSearch ? {width:'100px'} : {}}
                         onClick={()=>this.handleClickInput()}
                  />
                </div>
                {/*{searchInHeader}*/}
              </div>
              <div className="col-md-5 app-options">
                <nav className="app-nav">
                  {this.props.authenticated ? (
                      <ul>
                        <a className="btn-profile">
                          <img
                              src={currentUser
                                  ? currentUser.imageUrl
                                  : 'https://scontent.fvca1-2.fna.fbcdn.net/v/t1.0-9/57226516_130649268042776_946875555297361920_o.jpg?_nc_cat=110&_nc_oc=AQk9xTh7VU9OMW9GO0y3_Lw7sd1627-824YLpvz-MdAS5b4YRRVy-Oz-0IcIqRqNRqrLelyIgDDGvG4uYXQg3ghr&_nc_ht=scontent.fvca1-2.fna&oh=5a0d2ee5d36e31956be164b5c2d796cc&oe=5D9EE190'}
                              className="rounded-circle icon-profile"
                              alt="Username"/>
                        </a>
                        <li>
                          <NavLink to="/profile" style={{paddingLeft:"10px"}}>
                            {currentUser ? currentUser.name : "username"}
                          </NavLink>
                        </li>
                        <li>
                          <NavLink to="/home">Trang chủ</NavLink>
                        </li>
                        <li>
                          <NavLink to="/message">Tin Nhắn</NavLink>
                        </li>
                        <li>
                          <a onClick={this.props.onLogout} style={{color:'white'}}>Logout</a>
                        </li>
                      </ul>
                  ) : (
                      <ul>
                        <li>
                          <NavLink to="/login">Login</NavLink>
                        </li>
                        <li>
                          <NavLink to="/signup">Signup</NavLink>
                        </li>
                      </ul>
                  )}
                </nav>
              </div>
              <div className="col-md-2">
              </div>
            </div>
          </div>
        </header>
    )
  }
}

export default AppHeader;


